// Declare variables
$fontFamily: "Space Grotesk";
$fontSize: clamp(0.75rem, 2.3vw, 1.1rem);

// Main colors
$colorGreen: #b9ff66;
$colorGrey: #f3f3f3;
$colorDark: #191a23;
$colorBlack: #000000;
$colorWhite: #ffffff;

// Minimum page width 
$minWidth: 320;
// Canvas width 
$maxWidth: 1920;
// Limiting container width 
$maxWidthContainer: 1440;
// Indentation of the container (15 for right and left) 
$containerPadding: 30;

// Width of triggering of the first breakpoint
$containerWidth: $maxWidthContainer + $containerPadding;

// Break-points
$pc: em($containerWidth); // PC, laptop, large tablet with horizontal position
$tablet: em(991.98); // Tablet, mobile with horizontal position
$mobile: em(767.98); // Mobile L
$mobileSmall: em(479.98); // Mobile S